.hero {
    --photo-height: calc(1*646px);
    --photo-width: calc(1*479px);

    display: flex;

    background: url(../../images/photos/background-hero.svg);
    background-repeat: no-repeat;

    filter: drop-shadow(3px 3px 6px rgb(112, 109, 109));

}

.hero > div{
    position: relative;
    display: flex;
    justify-content: center;
}

.hero .photo {
    position: relative;
    z-index: 1;

    box-shadow: 3px 3px 6px rgb(112, 109, 109);
}

.hero .photo,
.hero .photo img {
    min-width: var(--photo-width);
    height: var(--photo-height);
}

.hero .name,
.hero .name-mobile {
    position: absolute;
    bottom: 0rem;
    
    min-width: var(--photo-width);
    display: flex;
    justify-content: center;

    font-family: title-font;
    font-weight: lighter;
    font-size: 4rem;
    color: white;

    text-shadow: 3px 3px 6px rgb(112, 109, 109);
}

.hero .name-mobile {
    display: none;
}

.hero .about-box {
    height: calc((var(--photo-height)*0.9));

    margin-top: calc((var(--photo-height)*0.05));
    margin-bottom: calc((var(--photo-height)*0.05));
    padding: 20px 50px 20px 30px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    color: var(--content-text-color);
    font-size: 1.3rem;
    background-color: white;
}

.hero .about-box h1{
    font-family: title-font;
    font-weight: lighter;
    font-size: 5rem;
    color: black;
}

.hero .about-box ul{
    align-self: end;

    display: flex;
    gap: 20px;
}

@media (max-width: 1200px) {
    .hero {
        display: block;
    }

    .hero .photo {
        margin: 0 1.5rem 0 0;

        float: left;
        position: relative;
    }

    .hero .photo,
    .hero .photo img {
        min-width: auto;
        max-width: calc(0.8*var(--photo-width));
        height: auto;
    }

    .hero .name {
        display: none;
    }

    .hero .name-mobile {
        display: block;
        position: absolute;

        z-index: 2;
        bottom: auto;
        top: 30px;
        right: 50px;

        font-size: max(80px, 5.5rem);
    }

    .hero .about-box {
        height: auto;

        display: block;
        margin-top: 120px;
        margin-left: 50px;
        padding: 90px 40px;

        font-size: max(20px, 1.4rem);
    }

    .hero .about-box h1 {
        text-align: center;
        
        font-size: max(45px, 3.5rem);
        padding-bottom: 20px;
    }

    .hero .about-box ul {
        display: none;
    }

}

@media (max-width: 700px) {
    .hero {
        display: flex;
        flex-direction: column;
        gap: 50px;

        background-size: 170vw 65vh;

        filter: none;
    }

    .hero .photo {
        padding: 0px 25px;
        margin: 0;

        float: none;

        display: flex;
        flex-direction: column;
        min-width: 100%;

        box-shadow: none;
    }

    .hero .photo img {
        max-width: none;
        width: 100%;
        object-fit: fill;

        box-shadow: 3px 3px 6px rgb(112, 109, 109);
    }

    .hero .name {
        display: flex;
        /* justify-content: start; */
        left: 50px;
        
        min-width: min-content;
        font-size: 10rem;
    }

    .hero .name-mobile {
        display: none;
    }

    .hero .about-box {
        display: flex;
        flex-direction: column;
        gap: 10px;

        padding: 0;
        margin: 0;

        background-color: unset;
    }

    .hero .about-box h1 {
        text-align: center;
        font-size: 70px;

        margin-bottom: -10px;

        padding: 0;
    }

    .hero .about-box span {
        font-size: 32px;
    }

    .hero .about-box ul {
        display: flex;
        align-self: center;
        gap: 30px;
    }
}